// Utils for integrating marketo forms
JP.marketoUtils = {
  cleanUpMarkup: function (form) {
    form.find('.mktoClear, .mktoOffset, .mktoGutter, .mktoAsterix').remove();
    form.find('input[type=text], input[type=email], input[type=number], textarea, select').addClass('form-control').attr('tabindex', '1');
    form.find('.btn').removeClass('btn').find('button').addClass('btn');
  },

  // Fetch marketo's official form descriptors (a JSON configuration file)
  // We use this to harvest official marketo field ID's
  getOfficialFormDescriptor: function (marketoFormId) {
    return $.get("https://app-sjp.marketo.com/index.php/form/getForm?munchkinId=704-VYP-527&form=" + marketoFormId);
  },

  // Retrieve Marketo field ID's using markup ID's
  // We assume that all Marketo form variants use the same field markup ID's, but have different marketo field IDs
  getMarketoFieldId: function (fieldName, marketoFormDescriptor) {

    // Sample row:
    // [{
    //   "Id": 11520,
    //   "Name": "Apple_Source__c",
    //   "Datatype": "hidden",
    //   "Maxlength": 18,
    //   "InputLabel": "Apple Source:",
    //   "InputInitialValue": "",
    //   "InputSourceChannel": "constant",
    //   "ProfilingFieldNumber": 0
    // }]
    // ( ^^ Note the wrapping array)
    var field = _.find(marketoFormDescriptor.rows, function (row) {
      return row[0]["Name"] === fieldName
    });

    if (!field) {
      console.error('Field with markup ID', fieldName, 'not found in marketoFormDescriptor');
    }

    return field[0]["Id"];
  }
};